import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Company } from '../models/company';
import { CompanyService } from '../services/company.service';
import { sha256 } from 'js-sha256';
import { ActionSheetController, AlertController, ModalController, NavController } from '@ionic/angular';
import { LoadingController } from '@ionic/angular';
import { Router } from '@angular/router';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { PopoverController } from '@ionic/angular';
import { PopovercomponentPage } from '../popovercomponent/popovercomponent.page'; 
import { PictureModalComponent } from '../picture-modal/picture-modal.component';
import { Crop } from '@ionic-native/crop/ngx';
import { File } from '@ionic-native/file/ngx';

declare var addCompany;

@Component({
  selector: 'app-presignin',
  templateUrl: './presignin.page.html',
  styleUrls: ['./presignin.page.scss'],
})
export class PresigninPage implements OnInit {

  presignForm: FormGroup;

  companyLogo : string;

  company = {} as Company;
  rcompanie: Company;

  cripto : string;

  vhash : string;

  hash = sha256.create();

  userPassword : string;

  croppedImagepath = "";
  isLoading = false;

  imagePickerOptions = {
    maximumImagesCount: 1,
    quality: 50
  };

  photo : string = '../../assets/logos/logohere.jpg';

  constructor(public formBuilder: FormBuilder, private companysservice: CompanyService,
    public atrCtrl: AlertController, public loadingController: LoadingController,
    private router:Router, private camera: Camera, private httpClient: HttpClient,
    private popover:PopoverController, private modalCtrl: ModalController, private crop: Crop, 
    public actionSheetController: ActionSheetController, private file: File) { }

  CreatePopover(){
     this.popover.create({component:PopovercomponentPage,
     showBackdrop:false}).then((popoverElement)=>{
       popoverElement.present();
     })
   }

   async callExternalJS(objCompany:any): Promise<string>{
    return await addCompany(objCompany);
        //console.log(ret);
  }

  cropImage(fileUrl) {
    this.crop.crop(fileUrl, { quality: 50 })
      .then(
        newPath => {
          this.showCroppedImage(newPath.split('?')[0])
        },
        error => {
          alert('Error cropping image' + error);
        }
      );
  }

  showCroppedImage(ImagePath) {
    this.isLoading = true;
    var copyPath = ImagePath;
    var splitPath = copyPath.split('/');
    var imageName = splitPath[splitPath.length - 1];
    var filePath = ImagePath.split(imageName)[0];

    this.file.readAsDataURL(filePath, imageName).then(base64 => {
      this.croppedImagepath = base64;
      this.photo = this.croppedImagepath;
      this.isLoading = false;
    }, error => {
      alert('Error in showing image' + error);
      this.isLoading = false;
    });
  }

  async selectImage() {
    const actionSheet = await this.actionSheetController.create({
      header: "Select Image source",
      buttons: [{
        text: 'Load from Library',
        handler: () => {
          this.pickImage(this.camera.PictureSourceType.PHOTOLIBRARY);
        }
      },
      {
        text: 'Use Camera',
        handler: () => {
          this.pickImage(this.camera.PictureSourceType.CAMERA);
        }
      },
      {
        text: 'Cancel',
        role: 'cancel'
      }
      ]
    });
    await actionSheet.present();
  }

  pickImage(sourceType) {
    const options: CameraOptions = {
      quality: 100,
      sourceType: sourceType,
      destinationType: this.camera.DestinationType.FILE_URI,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    }
    this.camera.getPicture(options).then((imageData) => {
      this.cropImage(imageData)
    }, (err) => {
      // Handle error
    });
  }
  
  insertCompany(objCompany:any){
    let ret = this.callExternalJS(objCompany);
    ret.then((res) => {
      this.showConfirm('CONGRATULATIONS','Registration Performed','You will soon receive an email to confirm your registration.');
      this.router.navigate(['/']);
    }).catch((err) =>{
      this.showConfirm('ERROR','Oops! An error has occurred!','Sorry about that. Please try again in a few moments.');
    })      
  }

  async openModal(){
    const modal = await this.modalCtrl.create({
      component: PictureModalComponent
    });

    await modal.present();
  }

  submitForm(){
    this.company.companyName = this.presignForm.get('companyname').value;
    this.company.companyLogin = this.presignForm.get('username').value;

    this.userPassword = this.presignForm.get('userpassword').value;
    this.hash.update('$6$rounds=5000$_ usesomesillystringforsalt$'+this.userPassword.trim());
    this.cripto = this.hash.hex();
    
    this.company.companyPassword = this.cripto;
    
    this.company.companyEmail = this.presignForm.get('email').value;

    this.company.companyCellPhone = this.presignForm.get('cel').value;
    var re = "data:image/jpeg;base64,";
    this.company.companyLogo = this.photo.replace(re,"");
    //console.log(this.company);
    this.insertCompany(this.company);
  }

  ngOnInit() {
    this.presignForm = new FormGroup({
      companyname : new FormControl(),//'', Validators.compose([Validators.required, CompanyValidator.isValid])),
      companytype : new FormControl(),
      username : new FormControl(),
      userpassword : new FormControl(),
      confirmuserpassword : new FormControl(),
      email : new FormControl(),
      cel : new FormControl(),
    });
  }

  async showConfirm(titulo:string, subtitulo:string, msg:string): Promise<any>{ 
    const alert = await this.atrCtrl.create({
      header:titulo,
      subHeader: subtitulo,
      message:msg,
      buttons: ['OK']
    });
    alert.present();
  }

  getGalerry(){
    const options: CameraOptions = {
      quality: 100,
      sourceType: this.camera.PictureSourceType.SAVEDPHOTOALBUM,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      allowEdit: true,
      targetWidth: 100,
      targetHeight: 100
    }

    this.camera.getPicture(options)
      .then((imageData) => {
        this.companyLogo = imageData;
        let base64image = 'data:image/jpeg;base64,' + imageData;
        this.photo = base64image;
      }, (error) => {
        return error;
      })
      .catch((error) => {
        console.error(error);
      })
  }

  getPhoto(){
    //console.log('opa');
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      allowEdit: true,
      targetWidth: 100,
      targetHeight: 100
    }

    this.camera.getPicture(options)
      .then((imageData) => {
        this.companyLogo = imageData;
        let base64image = 'data:image/jpeg;base64,' + imageData;
        this.photo = base64image;
      }, (error) => {
        return error;
      })
      .catch((error) => {
        console.error(error);
      })
  } 
}
